@charset "UTF-8";
@import url("reset.scss");
@import url("_global.scss");
/* font-family: 'Montserrat',
 sans-serif;
 font-family: 'Open Sans',
 sans-serif;
 */
*, *::after, *::before {
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
}

.slick-prev {
  display: none;
  opacity: 0;
}

.slick-next {
  display: none;
  opacity: 0;
}

.header {
  position: relative;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 50;
  font-size: 16px;
}
.header__body {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 80px;
  align-items: center;
}
.header__logo {
  flex: 0 0 auto;
  z-index: 3;
}
.header__logo img {
  max-width: 100%;
}
.header__burger {
  display: none;
}
.header__list {
  display: flex;
  justify-content: space-between;
  position: relative;
  z-index: 2;
}
.header__list li {
  list-style: none;
  margin: 0 0 0 20px;
}
.header__link {
  color: #fff;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 700;
  transition: all 0.3s linear;
}
.header__link:hover {
  font-size: 18px;
}

.header:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(42, 120, 164, 0.2);
  z-index: 2;
}

@media (max-width: 900px) {
  .body.lock {
    overflow: hidden;
  }

  .header__body {
    height: 50px;
  }

  .header__logo {
    align-self: flex-start;
    flex: 0 0 100px;
  }

  .header__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    z-index: 3;
  }

  .header__burger::before,
.header__burger::after {
    content: "";
    background-color: #fff;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transition: all 0.3s linear;
  }

  .header__burger::before {
    top: 1px;
  }

  .header__burger span {
    background-color: #fff;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    top: 9px;
    transition: all 0.3s linear;
  }

  .header__burger::after {
    bottom: 1px;
  }

  .header__burger.active::before {
    transform: rotate(45deg);
    top: 9px;
  }

  .header__burger.active span {
    transform: scale(0);
  }

  .header__burger.active::after {
    transform: rotate(-45deg);
    bottom: 9px;
  }

  .header__menu {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: #8799e4;
    padding: 70px 10px 20px 10px;
    transition: all 0.3s linear;
  }

  .header__menu.active {
    top: 0%;
  }

  .header__list {
    display: block;
  }

  .header__list li {
    margin: 0 0 20px 0;
  }
}
.about {
  width: 100%;
  background: #D4E4ED;
  height: 100vh;
  padding-top: 100px;
}
.about__inner {
  display: flex;
  row-gap: 60px;
  justify-content: space-between;
  align-items: center;
  margin: auto;
}
.about__info {
  font-family: "Montserrat", sans-serif;
  width: 430px;
}
.about__info-title {
  font-size: 64px;
  line-height: 68px;
  font-weight: 700;
  color: #302313;
  margin-bottom: 21px;
}
.about__info-text {
  font-size: 16px;
  line-height: 22px;
  color: #281F20;
  font-weight: 400;
  font-family: "Montserrat", sans-serif;
}
.about__design-slider {
  width: 630px;
}

/* Этапы работы */
.title {
  margin: 60px 0 60px 0;
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  font-family: "Montserrat", sans-serif;
}

.how-work__title {
  margin: 60px 0 60px 0;
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  font-family: "Montserrat", sans-serif;
}
.how-work__inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.how-work__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 27%;
  margin-bottom: 65px;
  position: relative;
}
.how-work__img-text {
  width: 192px;
  text-align: center;
}
.how-work__arrow {
  position: absolute;
  top: 35%;
  right: -10%;
}

/* Перечень услуг */
.services {
  padding-bottom: 56px;
}
.services__title {
  margin-top: 15px;
  margin-bottom: 50px;
}
.services__slider {
  text-align: center;
}

.slider {
  position: relative;
}
.slider__item {
  width: 350px;
  text-align: left;
  margin-right: 15px;
  margin-left: 15px;
}
.slider__img {
  margin-bottom: 25px;
}
.slider__img img {
  width: 350px;
  height: auto;
}
.slider__price {
  margin-bottom: 14px;
  font-size: 18px;
  line-height: 20px;
  font-weight: 700;
}
.slider__title {
  margin-bottom: 10px;
  font-size: 18px;
  line-height: 20px;
  font-weight: 700;
}
.slider__text {
  font-size: 16px;
  line-height: 22px;
  text-align: left;
  width: 350px;
}

.arrow-next {
  position: absolute;
  top: 45%;
  left: 100%;
}

.arrow-prev {
  position: absolute;
  top: 45%;
  right: 100%;
}

/* отзывы */
.review {
  padding-top: 59px;
  background-color: #D4E4ED;
  padding-bottom: 70px;
}
.review__inner {
  display: flex;
  justify-content: space-between;
  gap: 40px;
}
.review__item {
  border: 3px solid rgb(0, 0, 0);
  width: 353px;
  padding: 40px 30px 30px 30px;
}
.review__item-person {
  display: flex;
  justify-content: flex-start;
  gap: 10px;
  align-items: center;
  margin-bottom: 20px;
}
.review__item-name {
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
}
.review__item-text {
  font-size: 16px;
  line-height: 21px;
}

/* Контактные данные */
.contact {
  margin-bottom: 60px;
}
.contact__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 70px;
}
.contact__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 35px;
}
.contact__title {
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
}
.contact__item-link {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 30px;
}
.contact__item-link a {
  color: #000;
}
.contact__img {
  display: block;
  width: 52%;
}

.footer {
  background: #D4E4ED;
}
.footer__inner {
  padding: 20px 0 0 0;
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav__item a {
  color: #fff;
  text-transform: uppercase;
  font-size: 16px;
  transition: all 0.3s linear;
  font-weight: 700;
}
.nav__item a:hover {
  font-size: 18px;
}

@media (max-width: 1220px) {
  .how-work__inner {
    margin: 0 auto;
    justify-content: center;
  }
  .how-work__item {
    width: 50%;
    justify-content: center;
  }
  .how-work__img img {
    display: block;
    margin: 0 auto;
    height: auto;
  }
  .how-work__arrow {
    display: none;
  }

  .slider__item {
    margin: 10px auto;
  }
  .slider__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .slider__img img {
    max-width: 100%;
  }

  .arrow-next {
    left: 98%;
  }

  .arrow-prev {
    right: 98%;
  }
}
@media (max-width: 1070px) {
  .about__inner {
    justify-content: space-around;
  }

  .about__design-slider {
    width: 450px;
    height: auto;
  }

  .slick-dots {
    display: none;
  }

  .slick-dotted.slick-slider {
    margin-bottom: 0;
  }

  .contact__img img {
    width: 85%;
  }
}
@media (max-width: 900px) {
  .about {
    padding-top: 50px;
  }

  .about__inner {
    display: block;
  }

  .about__info {
    width: 100%;
    text-align: center;
  }
  .about__info-title {
    font-size: 45px;
  }
  .about__info-text {
    font-size: 16px;
  }

  .about__design-slider {
    margin: 10px auto 0 auto;
  }

  .how-work__img img {
    width: 80%;
  }

  .review__inner {
    flex-direction: column;
  }

  .review__item {
    width: 100%;
  }

  .nav {
    flex-direction: column;
    gap: 10px;
  }
}
@media (max-width: 795px) {
  .contact__img {
    display: none;
  }
}
@media (max-width: 795px) {
  .arrow-next, .arrow-prev {
    opacity: 0;
  }
}
@media (max-width: 740px) {
  .arrow-next,
.arrow-prev {
    opacity: 1;
  }
}
@media (max-width: 500px) {
  .about {
    padding-top: 50px;
  }

  .about__inner {
    display: block;
  }

  .about__info {
    width: 100%;
    text-align: center;
  }
  .about__info-title {
    padding-top: 10px;
    font-size: 30px;
    line-height: 100%;
  }
  .about__info-text {
    font-size: 16px;
  }

  .about__design-slider {
    padding-top: 10%;
    width: 100%;
  }

  .how-work__img img {
    width: 50%;
  }

  .how-work__inner {
    gap: 10px;
  }

  .arrow-next, .arrow-prev {
    opacity: 0;
  }
}