@import url("reset.scss");
@import url("_global.scss");

/* font-family: 'Montserrat',
 sans-serif;
 font-family: 'Open Sans',
 sans-serif;
 */
 *, *::after, *::before {
   box-sizing: border-box;
 }
body {
  font-family: 'Montserrat',
    sans-serif;
}

.slick-prev {
  display: none;
  opacity: 0;
}

.slick-next {
  display: none;
  opacity: 0;
}

.header {
  position: relative;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 50;
  font-size: 16px;
  &__body {
    position: relative;
    display: flex;
    justify-content: space-between;
    height: 80px;
    align-items: center;
  }

  &__logo {
    flex: 0 0 auto;
    z-index: 3;
  }

  &__logo img {
    max-width: 100%;
  }

  &__burger {
    display: none;
  }

  &__menu {
  }

  &__list {
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 2;
  }

  &__list li {
    list-style: none;
    margin: 0 0 0 20px;
  }
  
  &__link {
    color: #fff;
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 700;
    transition: all .3s linear;
  }

  &__link:hover {
    font-size: 18px;
  }
}

.header:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #2A78A433;
  z-index: 2;
}


@media(max-width: 900px) {
  .body.lock {
    overflow: hidden;
  }
  .header__body {
    height: 50px;
  }

  .header__logo {
    align-self: flex-start;
    flex: 0 0 100px;
  }

  .header__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    z-index: 3;
  }

  .header__burger::before,
  .header__burger::after {
    content: "";
    background-color: #fff;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transition: all .3s linear;
  }

  .header__burger::before {
    top: 1px;
  }

  .header__burger span {
    background-color: #fff;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    top: 9px;
    transition: all .3s linear;
  }

  .header__burger::after {
    bottom: 1px;
  }

  .header__burger.active::before {
    transform: rotate(45deg);
    top: 9px;
  }

  .header__burger.active span {
    transform: scale(0);
  }

  .header__burger.active::after {
    transform: rotate(-45deg);
    bottom: 9px;
  }

  .header__menu {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: #8799e4;
    padding: 70px 10px 20px 10px;
    transition: all 0.3s linear;
  }

  .header__menu.active {
    top: 0%;
  }

  .header__list {
    display: block;
  }

  .header__list li {
    margin: 0 0 20px 0;
  }
}





.about {
  width: 100%;
  background: #D4E4ED;
  height: 100vh;
  padding-top: 100px;
  &__inner {
    display: flex;
    row-gap: 60px;
    justify-content: space-between;
    align-items: center;
    margin: auto;
  }

  &__info {
    font-family: 'Montserrat', sans-serif;
    width: 430px;
  }

  &__info-title {
    font-size: 64px;
    line-height: 68px;
    font-weight: 700;
    color: #302313;
    margin-bottom: 21px;
  }

  &__info-text {
    font-size: 16px;
    line-height: 22px;
    color: #281F20;
    font-weight: 400;
    font-family: 'Montserrat', sans-serif;
  }

  &__design-slider {
    width: 630px;
  }

}

/* Этапы работы */
.title {
  margin: 60px 0 60px 0;
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  font-family: 'Montserrat', sans-serif;
}

.how-work {
  &__title {
    margin: 60px 0 60px 0;
    font-weight: 700;
    font-size: 32px;
    line-height: 39px;
    font-family: 'Montserrat',
      sans-serif;
  }

  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 27%;
    margin-bottom: 65px;
    position: relative;
  }

  &__img-text {
    width: 192px;
    text-align: center;
  }

  &__arrow {
    position: absolute;
    top: 35%;
    right: -10%;
  }
}

/* Перечень услуг */

.services {

  padding-bottom: 56px;

  &__title {
    margin-top: 15px;
    margin-bottom: 50px;
  }

  &__slider {
    text-align: center;

  }
}

.slider {
  position: relative;

  &__item {
    width: 350px;
    text-align: left;
    margin-right: 15px;
    margin-left: 15px;
  }

  &__img {
    margin-bottom: 25px;
  }

  &__img img {
    width: 350px;
    height: auto;
  }

  &__price {
    margin-bottom: 14px;
    font-size: 18px;
    line-height: 20px;
    font-weight: 700;
  }

  &__title {
    margin-bottom: 10px;
    font-size: 18px;
    line-height: 20px;
    font-weight: 700;

  }

  &__text {

    font-size: 16px;
    line-height: 22px;
    text-align: left;
    width: 350px;
  }
}

.arrow-next {
  position: absolute;
  top: 45%;
  left: 100%;
}

.arrow-prev {
  position: absolute;
  top: 45%;
  right: 100%;
}

/* отзывы */

.review {
  padding-top: 59px;
  background-color: #D4E4ED;
  padding-bottom: 70px;
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 40px;
  }

  &__item {
    border: 3px solid rgb(0, 0, 0);
    width: 353px;
    padding: 40px 30px 30px 30px;
  }

  &__item-person {
    display: flex;
    justify-content: flex-start;
    gap: 10px;
    align-items: center;
    margin-bottom: 20px;
  }

  &__item-name {
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;

  }

  &__item-text {
    font-size: 16px;
    line-height: 21px;
  }
}

/* Контактные данные */

.contact {
  margin-bottom: 60px;

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 70px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 35px;
  }

  &__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 39px;

  }

  &__item-link {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 30px;
  }

  &__item-link a {
    color: #000;
  }

  &__img {
    display: block;
    width: 52%;
  }
}

.footer {
  background: #D4E4ED;
  &__inner {
    padding: 20px 0 0 0;
  }
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__item a{
    color: #fff;
    text-transform: uppercase;
    font-size: 16px;
    transition: all 0.3s linear;
    font-weight: 700;
  }

  &__item a:hover {
    font-size: 18px;
  }
}



@media(max-width: 1220px) {
.how-work {
    &__title {}

    &__inner {
      margin: 0 auto;
     justify-content: center;

     }

    &__item {
      width: 50%;
      justify-content: center;
    }

    &__img img {
      display: block;
      margin: 0 auto;
      height: auto;
    }

    &__img-text {}

    &__arrow {
      display: none;
    }
  }
  

  .slider {
    &__item {
      margin: 10px auto;
     }
    
    &__inner {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    &__img img{
      max-width: 100%;
    }
   }

   .arrow-next {
     left: 98%;
   }

    .arrow-prev {
      right: 98%;
    }
}

@media(max-width: 1070px) {
  .about__inner {
    justify-content: space-around;
  }
  .about__design-slider {
    width: 450px;
    height: auto;

  }

  .slick-dots {
    display: none;
  }

  .slick-dotted.slick-slider {
    margin-bottom: 0;
  }

  .contact__img img{
    width: 85%;
  }
  
   
}

@media(max-width: 900px) {
  .about {
    padding-top: 50px;
  }
  .about__inner {
    display: block;
  }
  .about__info {
    width: 100%;
    text-align: center;
    &-title {
      font-size: 45px ;
    }
    &-text {
      font-size: 16px;
    }
  }
  .about__design-slider {
    margin: 10px auto 0 auto;
  }

  .how-work__img img {
    width: 80%;
  }

  .review__inner {
    flex-direction: column;
  }

  .review__item {
    width: 100%;
  }

  .nav {
    flex-direction: column;
    gap: 10px;
  }

}

@media(max-width: 795px) {
  .contact__img {
    display: none;
  }
}

@media(max-width: 795px) {
  .arrow-next, .arrow-prev {
    opacity: 0;
  }
}

@media(max-width: 740px) {
  .arrow-next,
  .arrow-prev {
    opacity: 1;
  }
}

@media(max-width: 500px) {
  .about {
      padding-top: 50px;
    }
  
    .about__inner {
      display: block;
    }
  
    .about__info {
      width: 100%;
      text-align: center;
  
      &-title {
        padding-top: 10px;
        font-size: 30px;
        line-height: 100%;
      }
  
      &-text {
        font-size: 16px;
      }
    }
  
  .about__design-slider {
    padding-top: 10%;
    width: 100%;
  }
  
  .how-work__img img {
    width: 50%;
  }

  .how-work__inner {
    gap: 10px;
  }

  .arrow-next, .arrow-prev {
    opacity: 0;
  }
}